@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100..900&display=swap');
*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
body{
  font-family:"Roboto",sans-serif;
   background-color: rgb(128, 183, 255);
}
.container{
  max-width:85%;
  margin:auto;
}
.flexSB{
   display: flex;
  justify-content: space-between;
} 
.icon{
  height:20px;
  width:20px;
  color:black;
  border-radius:50%;
  text-align:center;
  transition:0.5s;
  display:flexbox;
  justify-content: center;
  align-items:center;
  margin-block:3px;
  
}
.icon:hover{
  cursor:pointer;
  background-color:lightskyblue;
  color:#fff;
}
a{
  text-decoration:none;
}
.row {
  width: 50%;
}
button {
  padding: 17px 30px;
  background-color: #fff;
  color: #1eb2a6;
  font-weight: 600;
  border: none;
  margin: 30px 10px 0 0;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 24px 36px -11px rgb(0 0 0 / 9%);
  transition: 0.5s;
}
button i {
  margin-left: 20px;
}
button:hover {
  box-shadow: none;
}
.primary-btn {
  background-color: #1eb2a6;
  color: #fff;
}
#heading {
  text-align: center;
  padding: 40px 0;
}
#heading h3 {
  font-weight: 600;
  letter-spacing: 1px;
  color: #fafafa;
  text-transform: uppercase;
}
#heading h1 {
  font-size: 40px;
  margin: 20px 0;
  text-transform: capitalize;
}
p {
  line-height: 30px;
  font-size: 18px;
  text-align: center;
}
.back {
  background-image: url("../public/images/learn6.jpg");
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  padding-top: 30%;
  padding-right: 50px;
  color: #fff;
  text-align: center;
}
.back h1 {
  font-size: 100px;
  font-weight: 400;
}
.back h2 {
  font-weight: 500;
  font-size: 17px;
  text-transform: uppercase;
}
.margin{
  margin-top:40.3%;
}
.grid{
  display:grid;
  grid-template-columns:repeat(4,1fr) ;
  grid-gap:30px;
}
.flex{
  display:flex;
}
@media screen and (max-width: 768px) {
  .grid2,
  .grid3,
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .back {
    background-position: center;
    padding-top: 50% ;
    height: 80vh;
  }
}